html, body, #app, #app>div {
  height: 100%
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root{
  display: flex;
  flex-flow: column;
  height: 100%;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#header{
  display: flex; 
  justify-content: right;
  align-items: center;
  padding: 1em;
  flex: 0 1 auto;
  background-color: #012a4a;
}

.title{
  color: #fffbd9;
  right: 400px;
}

#title_one{
  font-size: 32px;
}

#content{
  background-color: #00447a; 
  padding: 39px; flex: 1 1 auto;
  border-style: none;
  display: flex;
}

#bottom_bar{
  min-width: 1080px;
  background-color: #004d89;
  display: inline-block;
  width: 98%;
  right: 11px;
  padding: 25px;
  border-radius: 40px;
  margin-top: auto;
  white-space: nowrap ;
}

#input_box{
  width: 97%;
  border-radius: 10px;
}

#send_button{
  left: 5px;
  background-color: #fffdde;
  border-radius: 18px;
  border-color: #ffffff;
}

#send_button_text{
  color: #012a4a;
}